import styled from "styled-components"

export const Container = styled.div`
	display: flex;
	margin-bottom: 64px;

	.col-image {
		align-self: center;
		img {
			width: 60%;
			border-radius: 5px;
			box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
		}

		button {
			float: left;
			margin-top: -30px;
			right: 20px;
			position: relative;
		}
	}

	.col-description {
		text-align: left;
		color: #444;
		font-family: "Montserrat", Sans-serif;
		font-size: 17px;
		font-weight: 400;
		line-height: 1.7em;

		ul {
			padding-inline-start: 40px;
		}
	}
`
export const List = styled.div`
	.container {
		display: block;
		position: relative;
		padding-left: 35px;
		margin-bottom: 12px;
		cursor: pointer;
		-webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
		font-family: montserrant;
		font-size: 16px;
	}

	.container input {
		position: absolute;
		opacity: 0;
		cursor: pointer;
		height: 0;
		width: 0;
	}

	.checkmark {
		position: absolute;
		top: 0;
		left: 0;
		height: 20px;
		width: 20px;
		background-color: #f4bf8c;
		border-radius: 5px;
	}

	.container:hover input ~ .checkmark {
		background-color: #13414f;
	}

	.container input:checked ~ .checkmark {
		background-color: #13414f;
	}

	.checkmark:after {
		content: "";
		position: absolute;
		display: none;
	}

	.container input:checked ~ .checkmark:after {
		display: block;
	}

	.container .checkmark:after {
		left: 8px;
		top: 4px;
		width: 5px;
		height: 10px;
		border: solid #eee;
		border-width: 0 3px 3px 0;
		-webkit-transform: rotate(45deg);
		-ms-transform: rotate(45deg);
		transform: rotate(45deg);
	}

	ul {
		list-style-type: none;
	}
`
