import React, { useEffect, useState } from "react"
import { PageProps } from "gatsby"

import Captcha from "react-google-recaptcha"

import Layout from "../../components/Layout"
import Banner from "../../components/utils/Banner"
import IntroductionContent from "../../components/utils/IntroductionContent"
import Button from "../../components/utils/Button"

import { Container, List } from "./styles"
import { useTranslation } from "react-i18next"
import { IBreadCrumbOption } from "../../interfaces"

interface IList {
	inputId: string
	inputName: string
	inputValue: string
	legislation: string
	legislationDescription: string
}

interface IPdfDownload {
	key: string
	value: string
}

function LegislationTemplate(props: PageProps) {
	const { subTypes }: any = props.pageContext
	const [title, setTitle] = useState("")
	const [thumbnailUrl, setThumbnailUrl] = useState("")
	const [content, setContent] = useState([])

	const [inputName, setInputName] = useState("")
	const [inputEnterprise, setInputEnterprise] = useState("")
	const [inputRole, setInputRole] = useState("")
	const [inputEmail, setInputEmail] = useState("")
	const [inputPhone, setInputPhone] = useState("")
	const [t, i18n] = useTranslation()
	console.log(title)

	const legislationToDownload: IPdfDownload[] = []
	const breadCrumbList: IBreadCrumbOption[] = [
		{
			url: "/",
			name: "Home",
			isActive: true,
		},
		{
			url: "legislation",
			name: t("breadCrumb.legislation"),
			isActive: true,
		},
		{
			url: title,
			name: t(`legislation.${title}`),
			isActive: false,
		},
	]

	useEffect(() => {
		subTypes.forEach((res: any) => {
			if (res.slug == props.path.split("legislation/")[1]) {
				setTitle(res.title)
				setThumbnailUrl(res.thumbnailUrl)
				setContent(res.content)
			}
		})
	}, [])

	const handleChange = (e: any) => {
		const key = e.target.id
		const value = e.target.value

		const fileToDownload = { key: key, value: value } as IPdfDownload

		if (legislationToDownload.length == 0) {
			legislationToDownload.push(fileToDownload)
		} else {
			const hasFileChecked = legislationToDownload.some(
				element => element.key == fileToDownload.key
			)
			if (hasFileChecked) {
				const index = legislationToDownload
					.map(file => {
						return file.key
					})
					.indexOf(fileToDownload.key)
				legislationToDownload.splice(index, 1)
			} else {
				legislationToDownload.push(fileToDownload)
			}
		}
	}

	const downloadLegislation = () => {
		setTimeout(() => {
			legislationToDownload.forEach(res => {
				fetch("https://www.kivalitaconsulting.com/" + res.value, {
					mode: "no-cors",
				})
					.then(resp => {
						return resp.blob()
					})
					.then(blob => {
						const url = window.URL.createObjectURL(blob)
						const a = document.createElement("a")
						a.style.display = "none"
						a.href = url
						a.download = res.key + ".pdf"
						document.body.appendChild(a)
						a.click()
						window.URL.revokeObjectURL(url)
					})
					.catch(() => console.log("Erro no download"))
			})
		}, 1000)
		setInputEnterprise("")
		setInputName("")
		setInputRole("")
		setInputPhone("")
		setInputEmail("")
	}

	return (
		<Layout breadCrumbList={breadCrumbList}>
			<Banner titlePage={t(`legislation.${title}`)} />

			<IntroductionContent
				subtitle={t(`introduction.findUs.subTitle`)}
				title={t(`legislation.${title}`)}
			/>

			<Container className="container">
				<div className="row">
					<div className="col-description col-6">
						<List>
							<ul>
								{content.map((res: IList) => (
									<li key={res.inputId}>
										<label className="container">
											<b>{res.legislation}</b> - {res.legislationDescription}
											<input
												name={res.inputName}
												id={res.inputId}
												value={res.inputValue}
												type="checkbox"
												onClick={e => handleChange(e)}
											/>
											<span className="checkmark"></span>
										</label>
									</li>
								))}
							</ul>
						</List>
					</div>

					<div className="col-image col-6 align-self-start">
						<img src={thumbnailUrl} alt="Thumbanail legslation" />
						<Button
							title={t("legislation.button")}
							bgColor={"#34e67f"}
							textColor={"#fff"}
							fontFamily="Poppins"
							fontSize={18}
							fontWeight={600}
							padding={"10px 30px"}
							id="prePopUpButton"
							dataTarget="#formModal"
							dataToggle="modal"
						/>
					</div>

					<div
						className="modal fade"
						id="formModal"
						role="dialog"
						aria-labelledby="exampleModalLabel"
						aria-hidden="true"
					>
						<div className="modal-dialog" role="document">
							<div className="modal-content">
								<div className="modal-header">
									<h5
										className="modal-title"
										id="exampleModalLabel"
										style={{ textAlign: "center" }}
									>
										{t("form.titleLegislation")}
									</h5>
									<button
										type="button"
										className="close"
										data-dismiss="modal"
										aria-label="Close"
									>
										<span aria-hidden="true">&times;</span>
									</button>
								</div>
								<div className="modal-body">
									<form id="downloadForm" name="formLegislacao">
										{" "}
										<div>
											<div className="form-group">
												{" "}
												<input
													size={1}
													type="text"
													value={inputName}
													onChange={e => setInputName(e.target.value)}
													className="form-control"
													placeholder={t("form.Nome Completo")}
													required={true}
													aria-required="true"
												/>
											</div>
											<div className="form-group">
												{" "}
												<input
													size={1}
													type="text"
													className="form-control"
													placeholder={t("form.Empresa")}
													value={inputEnterprise}
													onChange={e => setInputEnterprise(e.target.value)}
													required={true}
													aria-required="true"
												/>
											</div>
											<div className="form-group">
												{" "}
												<input
													size={1}
													type="text"
													className="form-control"
													placeholder={t("form.Cargo / Profissão")}
													value={inputRole}
													onChange={e => setInputRole(e.target.value)}
													required={true}
													aria-required="true"
												/>
											</div>
											<div className="form-group">
												{" "}
												<input
													size={1}
													type="email"
													className="form-control"
													placeholder={t("form.Email")}
													value={inputEmail}
													onChange={e => setInputEmail(e.target.value)}
													required={true}
													aria-required="true"
												/>
											</div>
											<div className="form-group">
												{" "}
												<input
													size={1}
													type="tel"
													className="form-control"
													placeholder={t("form.phone")}
													value={inputPhone}
													onChange={e => setInputPhone(e.target.value)}
													required={true}
													aria-required="true"
													pattern="[0-9()#&amp;+*-=.]+"
													title="Only numbers and phone characters (#, -, *, etc) are accepted."
												/>
											</div>
											<Captcha sitekey="6LevNN0UAAAAABhV0naYt9NOjRA9Q3u72vWrzP7s" />
											<div>
												{" "}
												<button
													type="button"
													className="btn btn-success fluid"
													id="downloadButton"
													onClick={() => downloadLegislation()}
													data-dismiss="modal"
													style={{ width: "100%", marginTop: 15 }}
												>
													{" "}
													<span>
														{" "}
														<span className=" elementor-button-icon">
															{" "}
														</span>{" "}
														<span className="elementor-button-text">
															Download
														</span>{" "}
													</span>{" "}
												</button>
											</div>
										</div>
									</form>
								</div>
							</div>
						</div>
					</div>
				</div>
			</Container>
		</Layout>
	)
}

export default LegislationTemplate
